function openUrl (downloadUrl) {
  // generateQRCode(downloadUrl)
  window.open(downloadUrl, "_self")
}




const url = `/getData/web/getWebPageConfig?appId=73&domain=${process.env.NODE_ENV !== 'development' ? window.location.host : 'slandingtest.easydish.com'}`

function getWebConfig () {
  axios.get(url).then(res => {
    if (res.status === 200 && res.data) {
      appleUrl = res?.data?.appStoreUrl;
      googleUrl = res?.data?.googlePlayUrl;

      if (getOs() === 'ios') {
        openUrl(appleUrl)
      } else {
        openUrl(googleUrl)
      }

    }
  })
}

getWebConfig();